.home-container {
  text-align: center;
  padding: 80px 20px;
  background-color: #0a0a0a; /* Midnight background */
  color: #e0e0e0; /* Soft light grey text */
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
}

.profile-photo {
  width: 180px;
  height: 180px;
  border-radius: 50%;
  margin-bottom: 30px;
  border: 4px solid #ffffff; /* White border for contrast */
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.6);
  object-fit: cover;
}

h1 {
  font-size: 3em;
  font-weight: 700;
  margin-bottom: 20px;
  color: #ffffff; /* Pure white */
  letter-spacing: -0.5px;
}

p {
  font-size: 1.5em;
  color: #e0e0e0;
  margin-bottom: 40px;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  line-height: 1.6;
}
