.contact {
    background-color: #0a0a0a; /* Midnight background */
    color: #e0e0e0; /* Soft light grey text */
    padding: 60px 20px;
    margin: 0; /* Remove margin to avoid white sides */
    width: 100%; /* Ensure it covers the full width */
    box-sizing: border-box; /* Ensure padding is included in the width */
    text-align: left;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
    line-height: 1.6;
}

.contact h2 {
    font-size: 2.5em;
    color: #ffffff; /* Pure white */
    margin-bottom: 40px;
    font-weight: 700;
    letter-spacing: -0.5px;
}

.contact p {
    font-size: 1.5em;
    color: #e0e0e0;
    margin-bottom: 20px;
}

.contact a {
    color: #007aff;  /* Appleâ€™s signature blue for links */
    text-decoration: none;
    font-weight: 600;
}

.contact a:hover {
    text-decoration: underline;
}

@media (min-width: 768px) {
    .contact {
        padding: 100px 40px;
    }

    .contact h2 {
        font-size: 3em;
    }

    .contact p {
        font-size: 1.5em;
    }
}
